  headerLeft,
  asideDisplay,
} from "@/core/helpers/config";

export default defineComponent({
  name: "KTHeader",
  components: {
    KTTopbar,
    KTMenu,
  },
  props: {
    title: String,
  },
  setup() {
    return {
      headerWidthFluid,
      headerLeft,
      asideDisplay,
      sita_path: document.location.origin,
    };
  },
